import React, { useEffect, useState } from 'react'
import "./row.css";
import axios from '../../../utils/axios';
import movieTrailer from 'movie-trailer';
import YouTube from 'react-youtube';

const Row = ({ title, fetchUrl, isLargeRow }) => {
    const [movies, setMovie] = useState([]);
    const [trailerUrl, setTrailerUrl] = useState("");

    const base_url = "https://image.tmdb.org/t/p/original/";
    
    useEffect(() => {
      async function fetchData() {
        const request = await axios.get(fetchUrl);
        // console.log(request)
        setMovie(request.data.results);
        return request;
      }
      fetchData();
    }, [fetchUrl]);

    const handelClick = (movie)=>{
      if(trailerUrl){
          setTrailerUrl('')
      }else{
          movieTrailer(movie?.title || movie?.orginal_name || movie?.name)
          .then((url)=>{
  const urlParams = new URLSearchParams(new URL(url).search)
  console.log(url)
  console.log(urlParams)
  setTrailerUrl(urlParams.get('v'))
          }) .catch(( error ) => {
            console.log(error)
          })
      }
    }
    
    const opts={
      height:"390",
      width:"100%",
      playerVars:{
        autoplay:1,
      },
    };

    return (
    <div className="row">
        <h1>{title}</h1>
        <div className="row__posters">
            {movies?.map((movie, index) => (
                <img 
                    onClick={() => handelClick(movie)}
                    key={index} src={`${base_url}${isLargeRow ? movie.poster_path : movie.backdrop_path}`} alt={movie.name} className={`row__poster $ {isLargeRow $$ "row__posterLarge"}`}
                />
            ))}
        </div>
        <div style={{ padding: '40px'}}>
          {trailerUrl && <YouTube videoId={trailerUrl} opts={opts} />}
        </div>
    </div>
    )
}


export default Row